
.top{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;

}
.topicon{
    font-size: 20px;
    margin-left: 10px;
    color: #444;
    cursor: pointer;
}
.topImg{
    height: 40px;
    width: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.topleft,.topright{
flex: 3;
display: flex;
align-items: center;
justify-content: center;

}
.topcenter{
    flex: 6;
}
.toplist{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}
.toplistitem{
    margin-right: 20px;
    font-size: 18;
    font-weight: 300;
    cursor: pointer;
}
.topSearchIcon{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
}